
.accordion-button:not(.collapsed)
    color: $color-white
    background-color: #212529
    box-shadow: none
.accordion-button:not(.collapsed)::after
    background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='#{$color-white}'><path fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/></svg>")

.description__card
    position: relative
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    height: 100%
    width: 100%
    background: $color-gray
    border-radius: 20px
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2)
    transition: 0.3s all
    &:hover
        transform: scale(1.04)
        box-shadow: 4px 4px 13px 0px rgba(34, 60, 80, 0.4)
.description_color-card
    background: $color-blue
.description__card-number
    position: absolute
    top: 0%
    left: 50%
    margin-right: -50%
    transform: translate(-50%, -50%)
    width: 40px
    height: 40px
    border-radius: 50px
    border: 4px solid white
    background: $color-green
    display: flex
    justify-content: center
    align-items: center
    z-index: 1

.description__number
    font-size: 22px
    color: $color-white

.description__container-card
    display: flex
    flex-direction: column
    align-items: center
.description__color-title
    color: $color-white
.description__color__text
    color: $color-white
.description__phone
    font-size: 22px
    color: $color-white
    text-decoration: none
    &:hover
        color: $color-yellow
.description__title-small
    font-size: 20px
.description__image-box
    height: 130px
    img
        max-width: 100%
        max-height: 100%
        height: auto
