.main
    &__container
        background: linear-gradient(180deg, rgba(233,232,232,0.96) 30%, rgba(249,249,250,0.6) 70%, rgba(233,232,232,0.8) 100%)
        position: relative
        display: flex
        justify-content: space-around
        align-items: center
        height: 580px
        border-radius: 20px
        padding: 20px

    &__septik_first
        position: absolute
        top: 56px
        left: 636px
        z-index: -3
        height: 480px

    &__septik_second
        position: absolute
        top: 80px
        left: 802px
        z-index: -2
        height: 480px

    &__first-box
        width: 700px

    &__title
        font-size: 44px

    &__box-text
        width: 360px

    &__text
        font-size: 20px
    &__box-image-text
        display: flex
        width: 360px
    &__button
        background: $color-yellow
        font-weight: 500
    &__image
        height: 640px

@include respond(desctop)
    .main__septik_second
        top: 80px
        left: 626px
        height: 480px
    .main__septik_first
        left: 500px
@include respond(tab-land)
    .main__septik_second
        top: 146px
        left: 557px
        height: 388px
    .main__septik_first
        top: 111px
        left: 430px
        height: 434px
@include respond(tab-port)
    .main__title
        font-size: 36px
    .main__septik_second
        display: none
    .main__septik_first
        left: 374px
@include respond(big-phone)
    .main__container
        height: 100%
    .main__septik_first
        left: 200px
        height: 400px
    .main__title
        font-size: 26px
    .main__image
        height: 540px
    .main__first-box
        width: 280px
    .header
        margin-bottom: 80px
@include respond(phone)
    .main__box-title
        width: 290px
    .main__septik_first
        display: none
    .main__image
        display: none
    .main__box-text
        width: 290px
        margin-bottom: 20px !important
    .main__box-image-text
        width: 290px
    .main__container
        justify-content: center
        height: 100%
    .main__title
        font-size: 20px
        margin-bottom: 30px !important
    .main__text
        font-size: 18px
    .card-title
        font-size: 18px
