.card
    border: none
    box-shadow: 2px 0px 8px -2px rgba(34, 60, 80, 0.3)
    transition: 0.3s all
    &:hover
        box-shadow: 0px 0px 13px 3px rgba(34, 60, 80, 0.2)

.card-body
    background: $color-light_gray

.models__number-persons-text
    display: flex
    align-items: flex-start
    &:after
        content: '................'
        display: block
        white-space: nowrap
        overflow: hidden

.models__reset
    &-text
        display: flex
        align-items: flex-start
        &:after
            content: '..............'
            display: block
            white-space: nowrap
            overflow: hidden

.models__processing-volume
    &-text
        display: flex
        align-items: flex-start
        &:after
            content: '...'
            display: block
            white-space: nowrap
            overflow: hidden

.models__price
    font-weight: 700
    color: $color-green

.models__card
    width: 100% !important
    height: 100%

.price_btn
    text-align: center
.next_link
    background-color: $color-btn-link
    display: block
    height: 40px
    width: 40px
    border-radius: 50%
    border: none
    position: relative
    transition: .3s
    &:hover
        background-color: $color-yellow
        transform: scale(1.1)
    &:before
        content: ""
        position: absolute
        top: 19px
        left: 10px
        width: 20px
        height: 2px
        background-color: #fff
        margin-right: 10px

    &:after
        content: ""
        position: absolute
        top: 14px
        left: 16px
        display: block
        height: 12px
        width: 12px
        box-sizing: border-box
        border: 2px solid #fff
        border-left-color: transparent
        border-top-color: transparent
        -webkit-transform: rotate(-45deg)
        transform: rotate(-45deg)
