.service__case
    width: 400px
    background: $color-gray
    border-radius: 20px
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2)

.service_img
    height: 80%
    opacity: 0.7
    border-radius: 20px
    transition: 0.3s all
    cursor: pointer
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2)
    &:hover
        opacity: 0.9
        transform: scale(1.04)
        box-shadow: 4px 4px 13px 0px rgba(34, 60, 80, 0.4)
