
.header
    background: $color-light_gray
    margin-bottom: 50px

.container_upper
    display: flex
    justify-content: space-between
    padding: 15px 5px
    border-bottom: 0.5px solid #c9c6c6b2
.container__box-first
    display: flex

.container__logo
    margin-right: 40px
.logo_image
    height: 54px

.container__text-logo
    display: flex
    align-items: center
    max-width: 260px

.container__box-second
    display: flex

.container__phone
    display: flex
    align-items: center
    font-size: 20px
    font-weight: 700
    margin-right: 40px
.container_phone
    text-decoration: none
    color: $color-black
.header__social
    display: flex
    align-items: center
.social_item
    text-decoration: none
    img
        height: 40px
.header_nav-link
    color: $color-black
    font-weight: 600
.header__mobail_container
    display: none
.offcanvas-header
    background: $color-yellow
.offcanvas-body
    background: $color-light_gray
.container_mobile-phone
    font-weight: 600
.nav-fill
    .active
        transform: scale(1.2)
@include respond(tab-port)
    .logo_image
        height: 36px
    .container__text-logo
        max-width: 220px
        font-size: 14px
    .container__phone
        font-size: 16px
        margin-right: 20px
    .social_item img
        height: 30px
@include respond(big-phone)
    .container_upper
        display: none
    .header_nav
        display: none
    .header__mobail_container
        display: block
