.nav-catalog__card
    background: $color-gray
    border-radius: 20px
    height: 100%
    text-align: center
    transition: 0.3s all
    box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2)
    &:hover
        box-shadow: 4px 4px 13px 0px rgba(34, 60, 80, 0.4)
    &_image
        overflow: hidden
        opacity: 0.8
        transition: 0.3s all
        &:hover
            opacity: 0.9
.card_image
    height: 100%
    width: 100%

.card_title a
    text-decoration: none
    color: $color-black

.page-link
    border-radius: 5px
    border: none
    font-weight: 500
    color: $color-black
